<template>
  <div class="form-create-order">
    <vue-form>
      <div class="form__switcher">
        <label>Кто нужен?</label>
        <vue-switcher v-model="orderSwitcherParams" />
      </div>
      <div class="first-option" v-if="orderSwitcherParams[0].isSwitch === true">
        <div class="form__location" v-if="!orderCheckBoxParams">
          <label>Адрес</label>
          <vue-text-input v-model="locationEnd" :placeholder="'Адрес'" />
        </div>
        <div class="form__date">
          <label>На какое число заказ?</label>
          <vue-number-input
            v-model="inputDate.date.value"
            v-model:is-valid="inputDate.isValid.value"
            :placeholder="'01.01.2025'"
            :type="'date'"
          />
        </div>
        <div class="form__time">
          <label>В какое время нужно прибыть?</label>
          <vue-number-input
            v-model="inputTime.time.value"
            v-model:is-valid="inputTime.isValid.value"
            :type="'time'"
            :placeholder="'12:00'"
          />
        </div>
        <div class="form__people">
          <label>Нужное количество грузчиков</label>
          <vue-number-input
            v-model="inputPeople.peopleCount.value"
            :placeholder="'Кол-во'"
            v-model:is-valid="inputPeople.isValid.value"
          />
        </div>
        <div class="form__description">
          <label>Описание проекта</label>
          <vue-text-area
            v-model="orderDescription.description.value"
            v-model:is-valid="orderDescription.isValid.value"
            :placeholder="'Что нужно сделать?'"
          />
        </div>
        <div class="form__working-time">
          <label>Ориентировочное время занятости (в минутах)</label>
          <vue-number-input
            v-model="inputWorkingTime.time.value"
            v-model:is-valid="inputWorkingTime.isValid.value"
            :placeholder="'60'"
          />
        </div>
        <div class="form__price">
          <label>Цена за заказ (руб)</label>
          <vue-number-input
            v-model="inputPrice.price.value"
            v-model:is-valid="inputPrice.isValid.value"
            :placeholder="'500'"
          />
        </div>
        <div class="form__save-template">
          <vue-black-button
            :title="'Сохранить как шаблон'"
            @click="savedTemplate"
          />
        </div>
        <div class="form__submit">
          <vue-yellow-button :title="'Создать'" />
        </div>
        <div class="form__substitute">
          <vue-green-button
            :title="'Подставить шаблон'"
            @click="isVisibleModal = !isVisibleModal"
          />
        </div>
      </div>

      <div
        class="second-option"
        v-if="orderSwitcherParams[1].isSwitch === true"
      >
        <div class="form__location-transportation__start">
          <label>Пункт "А"</label>
          <vue-text-input v-model="locationStart" :placeholder="'Адрес'" />
        </div>
        <div class="form__location-transportation__end">
          <label>Пункт "Б"</label>
          <vue-text-input v-model="locationEnd" :placeholder="'Адрес'" />
        </div>
        <div class="form__date">
          <label>На какое число заказ?</label>
          <vue-number-input
            v-model="inputDate.date.value"
            :placeholder="'01.01.2025'"
            :type="'date'"
            v-model:is-valid="inputDate.isValid.value"
          />
        </div>
        <div class="form__time">
          <label>В какое время нужно прибыть?</label>
          <vue-number-input
            v-model="inputTime.time.value"
            v-model:is-valid="inputTime.isValid.value"
            :type="'time'"
            :placeholder="'12:00'"
          />
        </div>
        <div class="form__people">
          <label>Нужное количество грузчиков (если нет, то впишите 0)</label>
          <vue-number-input
            v-model="inputPeople.peopleCount.value"
            :placeholder="'Кол-во'"
            v-model:is-valid="inputPeople.isValid.value"
          />
        </div>
        <div class="form__description">
          <label>Что перевозить?</label>
          <vue-text-area
            v-model="orderDescription.description.value"
            v-model:is-valid="orderDescription.isValid.value"
            :placeholder="'Что нужно сделать?'"
          />
        </div>
        <div class="form__price">
          <label>Цена за заказ (руб)</label>
          <vue-number-input
            v-model="inputPrice.price.value"
            v-model:is-valid="inputPrice.isValid.value"
            :placeholder="'500'"
          />
        </div>
        <div class="form__save-template">
          <vue-black-button
            :title="'Сохранить как шаблон'"
            @click="savedTemplate"
          />
        </div>
        <div class="form__submit">
          <vue-yellow-button :title="'Создать'" />
        </div>
        <div class="form__substitute">
          <vue-green-button
            :title="'Подставить шаблон'"
            @click="isVisibleModal = !isVisibleModal"
          />
        </div>
      </div>
      <vue-modal
        :is-visible="isVisibleModal"
        @close-modal="isVisibleModal = $event"
      >
        <div class="selection-template">
          <div class="selection-template__title h3">
            <h3>Использовать шаблон:</h3>
          </div>
          <div class="selection-template__items">
            <div
              class="selection-template__item"
              v-for="template of templateStore.templates"
              :key="template.id"
              @click="autoFillTemplate(template)"
            >
              <p>{{ template.name }}</p>
            </div>
          </div>
        </div>
      </vue-modal>
      <vue-modal
        :is-visible="isSaveAsTemplate"
        @close-modal="isSaveAsTemplate = $event"
      >
        <div class="created-template">
          <vue-text-input
            v-model="templateName"
            :placeholder="'Введите название шаблона'"
          />
          <vue-black-button :title="'Сохранить'" @click="savedTemplate" />
        </div>
      </vue-modal>
    </vue-form>
  </div>
</template>

<script setup lang="ts">
import VueForm from "@/components/Forms/VueForm.vue";
import VueTextInput from "@/components/Inputs/VueTextInput.vue";
import VueTextArea from "@/components/Inputs/VueTextArea.vue";
import VueSwitcher from "@/components/Switchers/VueSwitcher.vue";
import VueNumberInput from "@/components/Inputs/VueNumberInput.vue";
import VueYellowButton from "@/components/Buttons/VueYellowButton.vue";
import VueBlackButton from "../Buttons/VueBlackButton.vue";
import VueGreenButton from "../Buttons/VueGreenButton.vue";
import VueModal from "../Modals/VueModal.vue";

import { ref } from "vue";
import { userTemplatesStore } from "@/store/template";
import { Template } from "@/interfaces/Template";
import { toast } from "vue3-toastify";
import { TemplateBase } from "@/interfaces/TemplateBase";

const templateStore = userTemplatesStore();
let isVisibleModal = ref(false);
let isSaveAsTemplate = ref(false);
const templateName = ref("");
const orderSwitcherParams = ref([
  {
    id: 1,
    value: "Грузчики",
    isSwitch: false,
  },
  {
    id: 2,
    value: "Грузоперевозки",
    isSwitch: false,
  },
]);
//input text area
const orderDescription = {
  description: ref(""),
  isValid: ref(false),
};
// input text
const locationStart = ref("");
const locationEnd = ref("");
// inputs number
const inputPeople = {
  peopleCount: ref<number | null>(null),
  isValid: ref(false),
};
const inputPrice = {
  price: ref<number | null>(null),
  isValid: ref(false),
};
const inputDate = {
  date: ref(""),
  isValid: ref(false),
};
const inputWorkingTime = {
  time: ref<number | null>(null),
  isValid: ref(false),
};
const inputTime = {
  time: ref(""),
  isValid: ref(false),
};
const orderCheckBoxParams = ref(null);

const savedTemplate = () => {
  isSaveAsTemplate.value = !isSaveAsTemplate.value;
  if (templateName.value) {
    const model = createTemplate();
    templateStore.createdTemplate(model);
    toast("Сохранен как шаблон!", {
      theme: "auto",
      type: "success",
      position: "top-center",
      transition: "flip",
    });
  } else {
    isSaveAsTemplate.value = true;
  }
};

const createTemplate = () => {
  const model: TemplateBase = {
    locationStart: locationStart.value,
    locationEnd: locationEnd.value,
    time: inputTime.time.value,
    name: templateName.value,
    sumPeople: inputPeople.peopleCount.value,
    description: orderDescription.description.value,
    workers: {
      value: orderSwitcherParams.value[0].isSwitch
        ? "Грузчики"
        : "Грузоперевозки",
    },
    workingHours: Number(inputWorkingTime.time.value),
    priceInfo: {
      price: inputPrice.price.value,
    },
  };
  return model;
};

const autoFillTemplate = (t: Template) => {
  //заполняем поля из шаблона
  if (t.workers.value === "Грузчики") {
    orderSwitcherParams.value[0].isSwitch = true;
    orderSwitcherParams.value[1].isSwitch = false;
  } else if (t.workers.value === "Грузоперевозки") {
    orderSwitcherParams.value[1].isSwitch = true;
    orderSwitcherParams.value[0].isSwitch = false;
  }

  orderDescription.description.value = t.description;
  locationStart.value = t.locationStart ? t.locationStart : "";
  locationEnd.value = t.locationEnd;
  inputPeople.peopleCount.value = t.sumPeople;
  inputPrice.price.value = t.priceInfo.price;
  inputWorkingTime.time.value = t.workingHours;
  inputTime.time.value = t.time;

  isVisibleModal.value = false;
};
</script>

